.application-block {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr .3fr;
    border-bottom: 1px solid rgb(36, 36, 36);
    justify-items: center;
}
.application-block > div:hover{
    background: rgb(239, 209, 76);
    cursor: pointer;
    color: #000;
    border-radius: 5px;
}
.application-block p {
    padding: 1em 2em;
    margin: 0;
}
.edit-icon {
    padding: .75em;
    height: 3.5em;
}

/* EDITABLE AREA */
.editableArea-container {
    /*  */
    background: rgba(238, 238, 238, 0.85);
    /*  */
    border-bottom: 1px solid rgb(36, 36, 36);
    position: absolute;
    top: 0;
    right:0;
    bottom:0;
    left:0;
    z-index: 2;
}

.editable-content {
    width: 75%;
    margin: 5em auto;
    background: #fff;
    border: 2px solid rgb(36, 36, 36);
    border-radius: 2px;
}
.icon-update {
    float: right;
    padding: 1em;
}
.icon-update img {
    height: 2em;
}
.icon-update:hover {
    cursor: pointer;
}

/* EDITABLE FORM */
.editable-form {
    padding: 2em;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1em;
}
.editable-form input, select, option {
    font-size: 1.2em;
    margin: .2em;
    border: 1px solid rgb(36, 36, 36);
    border-radius: 3px;
}
.applicationStatus {
    display: block;
}
.hint {
    text-align: center;
}

@media (max-width: 500px) {
    .editable-content {
        width: 90%;
    }
    .application-list {
        font-size: .6em;
    }
    .editable-form {
        grid-template-columns: 1fr;
        grid-gap: .5em;
        padding: 0;
    }
    .editable-form p {
        margin: 0;
        padding: 0;
    }
}

.list-head {
    font-weight: bold;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr .3fr;
    border-bottom: 1px solid rgb(36, 36, 36);
    justify-items: center;
}
.track-hub {
    margin-top: 2em;
}

.application-list {
    list-style: none;
    margin: 0;
    padding: 0;
    color: rgb(36, 36, 36);
}
/* Add New button */
.add-new {
    border-right: 1px solid #000;
    border-bottom: 1px solid #000;
    float: left;
    width: 15em;
    text-align: center;
    border-radius: 5px;
    background: rgba(239, 209, 76, 0.397);
    color: rgb(36, 36, 36);
}
.add-new p {
    padding-top: .3em;
}
.add-new:hover {
    background: rgb(239, 209, 76);
    cursor: pointer;
    color: #000;
}
.add-icon {
    height: 2em;
}
.add-icon-container {
    float: left;
    margin: 1em .5em 1em 1em;
}
/* EDITABLE FORM */
.editable-form input, select, option {
    font-size: 1.2em;
    margin: .2em;
    border: 1px solid rgb(36, 36, 36);
    border-radius: 3px;
}

/* ICONS */
.icons-container {
    height: 4em;
}
.icon-save {
    float: right;
    padding: 1em;
}
.icon-save img {
    height: 2em;
}
.icon-save, .icon-close:hover {
    cursor: pointer;
}
.icon-close {
    float: left;
    padding: 1em;
}
.icon-close img {
    height: 2em;
}

@media (max-width: 500px) {
    .editable-content {
        margin: 10em auto;
    }
}
.nav-wrapper {
    border-bottom: 2px solid #000;
}

@media (max-width: 500px) {
    nav .brand-logo {
        font-size: 1.1rem;
    }
    nav ul a {
        font-size: .8rem;
    }
}
/*  */
body {
    margin: 0;
    padding: 0;
    /* font-family: 'Nunito', sans-serif; */
    font-family: 'Open Sans', sans-serif;
}


